import { FilterHook } from "Utils"
import { useSubscriptionsBuyButtonState } from "../_state/hooks"

const SubscriptionTooltip = wp.element.lazy(() =>
  import(
    /* webpackChunkName: 'SubscriptionTooltip-public' */ "../../../../../tooltip"
  )
)

function SubscriptionDetails() {
  const state = useSubscriptionsBuyButtonState()
  return (
    <SubscriptionTooltip
      label={state.settings.subscriptionsDetailsLabel}
      options={{ placement: "right" }}
    >
      <FilterHook name="product.subscriptionsInfoHTML">
        <>
          <strong>{state.settings.subscriptionsDetailsHeading}</strong>
          <p>{state.settings.subscriptionsDetailsText}</p>
        </>
      </FilterHook>
    </SubscriptionTooltip>
  )
}

export default SubscriptionDetails
